.container2{
    width: 100%;
    height: 80%;
    position: relative;
    font-family: 'FuenteOP';
}
.container2 h1{
    margin-top: 20px;
    color: #5e2129;
}
.cards{
    display:flex;
    justify-content: space-around;
    align-items:center;
    height: 70%;
}
.card{
    position: relative;
    background: none;
}
.card h3{
    color: #5e2129;
}
.card a{
    color: rgb(78, 77, 77);
    font-size: 30px;
}
.card p{
    font-size: 30px;
}
.btn1{
    font-size: 30px;
    width: 30%;
    background-color: #5e2129;
}
.Modal1{
    max-width: 95% !important;
}
